<template>
  <div class="container" @click="nextStep">
    <div class="role__box">
      <div class="role_team1_box">
        <img
          class="role_team1_hole"
          src="../../assets/images/role/hole.svg"
          alt="hole"
        />
        <img
          class="role_team1_img"
          src="../../assets/images/role/role_team1.png"
          alt="role"
        />
        <img
          class="role_team1_light"
          src="../../assets/images/role/role_team1_light.png"
          alt="role"
        />
      </div>
    </div>
    <div class="dialog__ee">
      <div class="dialog__content__ee">
        <p class="tag__ee">EE</p>
        <template v-if="step === 0">
          <p>
            等等等等等 ， 你都還不知道什麼是 Sprint 吧 ！ 讓我先為你介紹一下～
            仔細聽好唷 ， 等等會考考你 ！
          </p>
        </template>
        <template v-if="step === 1">
          <p>
            Sprint 是一個短衝 ， 開發團隊會在這期間執行開發 。 在這段期間內 ，
            開發團隊舉辦
            <span class="primary-color">每日站立會議 (Daily Scrum)</span>
            ， 追蹤成員間的工作狀況 ， 在 Sprint 的結束也會包含
            <span class="primary-color"> 短衝檢視會議(Sprint Review) </span>
            以及
            <span class="primary-color"
              >短衝自省會議 (Sprint Retrospective)</span
            >
            。
          </p>
        </template>
        <template v-if="step === 2">
          <p>
            優化工作流程、讓團隊有變得更好的機會。 推薦工具 ：
            <img
              src="../../assets/images/AttachedPic/logo_confluence_w.png"
              alt=""
            />
          </p>
        </template>
        <template v-if="step === 3">
          <span class="h2">換你來試試看吧 ！</span>
          <p>
            在這經典的 Surum 流程圖中 ， 這些流程分別代表哪一個會議呢 ？ 提示 ：
            把右側的三個流程拖移至正確的位置上吧 ！
          </p>
        </template>
        <template v-if="step === 4">
          <p>哼哼沒想到你這麼快就學會惹 ， 快結束了加油加油 ！</p>
        </template>
        <div class="next_dialog_box">
          <img
            class="next_dialog_img"
            src="../../assets/images/gif/ic_continue_team1.gif"
            alt="ic_continue_po"
          />
        </div>
      </div>
    </div>
    <template v-if="step === 1 || step === 2">
      <div class="cards">
        <div class="card">
          <img src="../../assets/images/AttachedPic/sprint_daily.png" alt="" />
          <div class="title">
            <h3>每日站立會議</h3>
            <p>Daily Scrum</p>
          </div>
          <div class="des">
            <p>每天都要進行的會議 ， 以15分鐘為限制</p>
            <ul>
              <li>．昨天為團隊的短衝目標 (Sprint Goal)做了那些進度</li>
              <li>．今天我會如何準備來幫助團隊達到短衝目標</li>
              <li>．過程中有遇到什麼問題、難題</li>
            </ul>
            透過團隊分享 ， 追蹤大家的工作狀況。
          </div>
        </div>
        <div class="card">
          <img src="../../assets/images/AttachedPic/sprint_review.png" alt="" />
          <div class="title">
            <h3>短衝檢視會議</h3>
            <p>Sprint Review</p>
          </div>
          <div class="des">
            <p>用來檢視該次短衝增量的成果 ， 以蒐集相關的回饋數據或意見 。</p>
          </div>
        </div>
        <div class="card">
          <img src="../../assets/images/AttachedPic/sprint_retro.png" alt="" />
          <div class="title">
            <h3>短衝自省會議</h3>
            <p>Sprint Retrospective</p>
          </div>
          <div class="des">
            <p>團隊在自省會議裡 , 會共同回顧該短衝歷程發生的事情</p>
            <ul>
              <li>．好的地方</li>
              <li>．可以改進的地方</li>
              <li>．如何維持我們已有的成功經驗</li>
            </ul>
          </div>
        </div>
      </div>
    </template>
    <template v-if="step === 3">
      <div class="task_box">
        <div class="task_step">
          <img
            class="task_img"
            src="../../assets/images/AttachedPic/sprint_process.png"
            alt=""
          />
          <div class="step_box box_1">
            <p class="title">產品代辦清單</p>
            <p class="sub_title">Product Backlog</p>
          </div>
          <div class="step_box box_2">
            <p class="title">短衝規劃會議</p>
            <p class="sub_title">Sprint Planning</p>
          </div>
          <div class="step_box box_3">
            <p class="title">短衝代辦事項</p>
            <p class="sub_title">Sprint Backlog</p>
          </div>
          <div class="step_box box_4">
            <p class="title">短衝</p>
            <p class="sub_title">Sprint</p>
          </div>
          <draggable
            :list="targetList"
            :group="group_target"
            class="list"
            item-key="id"
          >
            <template #item="{ element }">
              <div class="list__item" :class="element.style">
                <p class="h3">{{ element.title }}</p>
                <p class="">{{ element.sub_title }}</p>
              </div>
            </template>
          </draggable>

          <draggable
            :list="dataList"
            :group="group_drag"
            class="list draggableList"
            item-key="id"
          >
            <template #item="{ element }">
              <div class="list__item">
                <p class="h3">{{ element.title }}</p>
                <p class="">{{ element.sub_title }}</p>
              </div>
            </template>
          </draggable>
        </div>
      </div>
    </template>
  </div>
  <button
    v-if="step === 2"
    type="button"
    class="primary-btn"
    v-on:click.stop="practiceAction"
  ></button>
  <button
    v-if="isFinished"
    type="button"
    class="primary-btn finished-btn"
    v-on:click.stop="nextStep"
  ></button>
  <div class="mask" v-on:click="nextPage">
    <button class="primary-btn-outline">點擊畫面任意處繼續</button>
  </div>
</template>
<script>
import draggable from "vuedraggable";
export default {
  components: {
    draggable,
  },
  emits: ["next-step"],
  data() {
    return {
      step: 0,
      isFinished: false,
      targetList: [
        { style: "drag_1" },
        { style: "drag_2" },
        { style: "drag_3" },
      ],
      dataList: [
        {
          title: "每日站立會議",
          sub_title: "Daily Scrum",
          style: "drag_1 drag",
        },
        {
          title: "短衝檢視會議",
          sub_title: "Sprint Review",
          style: "drag_2 drag",
        },
        {
          title: "短衝自省會議",
          sub_title: "Sprint Retrospective",
          style: "drag_3 drag",
        },
      ],
      group_target: {
        name: "targetBox",
        put: true,
        pull: () => {},
      },
      group_drag: {
        name: "dragItem",
        pull: () => {},
      },
    };
  },
  methods: {
    nextStep() {
      if (this.step === 0) {
        this.step = 1;
      } else if (this.step === 1) {
        this.step = 2;
      } else if (this.step === 3) {
        this.isFinished = false;
        this.step = 4;
        let mask = document.getElementsByClassName("mask").item(0);
        setTimeout(() => {
          mask.classList.add("show");
        }, 500);
      }
    },
    nextPage() {
      this.$emit("next-step", 5);
    },
    practiceAction() {
      this.step = 3;
    },
  },
  watch: {
    targetList: {
      handler: function (val) {
        if (val.length === 6) {
          this.isFinished = true;
        }
      },
      deep: true,
    },
  },
};
</script>
<style lang="scss" scoped>
.primary-btn {
  position: absolute;
  right: 40px;
  bottom: 10vh;
  z-index: 1000;
  &::after {
    content: "練習去囉";
  }
  &::before {
    content: "練習去囉";
  }
}

.finished-btn {
  &::after {
    content: "我完成了";
  }
  &::before {
    content: "我完成了";
  }
}
.role__box {
  height: 300px;
}
.role__box .role_team1 {
  &_hole,
  &_img,
  &_light {
    position: absolute;
  }
  &_hole {
    width: 15vw;
    top: 40px;
    right: 40px;
  }
  &_img {
    width: 15vw;
    z-index: 2;
    top: 40px;
    right: 30px;
  }
  &_light {
    width: 20vw;
    margin-left: -3%;
    top: 40px;
    right: 5px;
  }
}

.dialog__ee {
  position: absolute;
  width: 70vw;
  left: 5vw;
  top: 40px;
  z-index: 1000;
  &__content {
    padding: 40px 40px 40px 100px;
  }
  .tag__ee {
    position: absolute;
    color: #0a0d14;
    top: 40.5px;
    left: -7px;
    background: #ffc700;
    padding: 4px 24px;
  }
  .next_dialog_box {
    margin-top: -43px;
    display: flex;
    justify-content: flex-end;
    img {
      width: 2rem;
      cursor: pointer;
    }
  }
}

.dialog__content__ee {
  padding: 40px 40px 40px 100px;
}

.cards {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 0 70px;
  width: 100%;
  gap: 80px;
  position: absolute;
  bottom: 8%;
  .card {
    max-width: calc((100% / 3) - 160px);
    img {
      width: 400px;
    }
    .title {
      background: rgba(10, 13, 20, 0.6);
      border: 2px solid #ffc700;
      text-align: center;
      border-radius: 20px;
      padding: 8px;
      h3 {
        color: #ffc700;
        font-size: 32px;
        line-height: 48px;
        letter-spacing: 0.2rem;
      }
      p {
        color: #00ffe0;
        font-size: 16px;
        line-height: 22px;
        letter-spacing: 0.2rem;
      }
    }
    .des {
      margin-top: 20px;
      color: #ffffff;
      font-size: 20px;
      line-height: 36px;
      letter-spacing: 0.2rem;
    }
  }
}
.task_box {
  margin-left: 100px;
  height: calc(100vh - 300px);
  .task_step {
    width: 950px;
    position: relative;
    .task_img {
      width: 100%;
      height: 100%;
    }
    .step_box {
      position: absolute;
      width: 324px;
      height: 94px;
      border: 2px solid #00ffe0;
      background: rgba(10, 13, 20, 0.6);
      border-radius: 20px;
      text-align: center;
      padding: 8px;
      .title {
        color: #00ffe0;
        font-size: 32px;
        line-height: 48px;
      }
      .sub_title {
        color: #008d96;
      }
    }
    .box_1 {
      top: 0px;
      left: 50px;
      &::after {
        content: "";
        width: 47px;
        height: 5px;
        position: absolute;
        top: 50px;
        left: -50px;
        display: block;
        background-color: #00ffe0;
      }
    }
    .box_2 {
      top: 120px;
      left: 50px;
      &::after {
        content: "";
        width: 47px;
        height: 5px;
        position: absolute;
        top: 50px;
        left: -50px;
        display: block;
        background-color: #00ffe0;
      }
    }
    .box_3 {
      top: 240px;
      left: 50px;
      &::after {
        content: "";
        width: 47px;
        height: 5px;
        position: absolute;
        top: 50px;
        left: -50px;
        display: block;
        background-color: #00ffe0;
      }
    }
    .box_4 {
      top: 420px;
      left: 180px;
      width: 150px;
      &::after {
        content: "";
        height: 55px;
        width: 5px;
        top: -55px;
        left: 50%;
        position: absolute;
        display: block;
        background-color: #00ffe0;
      }
    }
  }
}

.list {
  display: flex;
  flex-direction: column;
  gap: 16px;
  position: absolute;
  top: 0;
  &__item {
    border: 2px dashed #00ffe0;
    border-radius: 20px;
    padding: 12px;
    font-size: 20px;
    line-height: 36px;
    letter-spacing: 0.2rem;
    font-weight: 500;
    height: 96px;
    display: flex;
    align-items: center;
    cursor: pointer;
    width: 300px;
    &.drag_1 {
      position: absolute;
      top: 300px;
      left: 450px;
      &::after {
        content: "";
        width: 35px;
        height: 5px;
        background-color: #00ffe0;
        position: absolute;
        left: -35px;
        top: 50px;
      }
    }
    &.drag_2 {
      position: absolute;
      top: 450px;
      left: 450px;
      &::after {
        content: "";
        height: 50px;
        width: 5px;
        background-color: #00ffe0;
        position: absolute;
        top: 95px;
        left: 50%;
      }
    }
    &.drag_3 {
      position: absolute;
      top: 450px;
      left: 780px;
      &::after {
        content: "";
        height: 50px;
        width: 5px;
        background-color: #00ffe0;
        position: absolute;
        top: 95px;
        left: 50%;
      }
    }
    &.drag {
      background: rgba(10, 13, 20, 0.6);
    }
    .text {
      margin-left: 20px;
      width: calc(100% - 70px);
    }
  }
  .hide {
    display: none;
  }
}

.draggableList {
  top: 25px;
  right: -400px;
  .list__item {
    background: rgba(10, 13, 20, 0.6);
    border: 2px solid #ffc700;
    text-align: center;
    border-radius: 20px;
    padding: 8px;
    display: flex;
    flex-direction: column;
    .h3 {
      color: #ffc700;
      font-size: 32px;
      line-height: 48px;
      letter-spacing: 0.2rem;
    }
    p {
      color: #00ffe0;
      font-size: 16px;
      line-height: 22px;
      letter-spacing: 0.2rem;
    }
  }
}

.list {
  .list__item.drag {
    background: rgba(10, 13, 20, 0.6);
    border: 2px solid #ffc700;
    text-align: center;
    border-radius: 20px;
    padding: 8px;
    display: flex;
    flex-direction: column;
    .h3 {
      color: #ffc700;
      font-size: 32px;
      line-height: 48px;
      letter-spacing: 0.2rem;
    }
    p {
      color: #00ffe0;
      font-size: 16px;
      line-height: 22px;
      letter-spacing: 0.2rem;
    }
  }
}
.mask {
  position: absolute;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 2050;
  background: linear-gradient(
    180deg,
    rgba(0, 255, 224, 0) 0%,
    rgba(0, 255, 224, 0.25) 40%,
    rgba(0, 255, 224, 0.45) 60%,
    rgba(0, 255, 224, 0.7) 80%,
    rgba(0, 255, 224, 0.9) 100%
  );
  display: none;
  &.show {
    display: block;
  }
  button {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
</style>
