        />
        <img
          class="role_team1_img"
          src="../../assets/images/role/role_team1.png"
          alt="role"
        />
        <img
          class="role_team1_light"
          src="../../assets/images/role/role_team1_light.png"
          alt="role"
        />
      </div>
      <div class="role_team2_box" v-if="step === 2 || step === 3 || step === 4">
        <img
          class="role_team2_hole"
