                alt=""
              />
              <p class="num">2</p>
            </template>
          </div>
          <div class="storyImg_3">
            <img src="../../assets/images/AttachedPic/story.png" alt="story" />
            <template v-if="step === 4">
              <img
                class="timeImg"
                src="../../assets/images/AttachedPic/time_3.png"
                alt=""
              />
              <p class="num">3</p>
            </template>
          </div>
        </div>
        <div class="story2">
          <div class="storyImg_4">
            <img src="../../assets/images/AttachedPic/story.png" alt="story" />
            <template v-if="step === 4">
              <img
                class="timeImg"
                src="../../assets/images/AttachedPic/time_5.png"
                alt=""
              />
              <p class="num">5</p>
            </template>
          </div>
          <div class="storyImg_5">
