              />
              <p class="num num21">13</p>
            </div>
          </template>
        </div>
      </div>
    </div>
    <div class="dialog" v-if="step === 0">
      <div class="dialog__content">
        <p class="tag">PO</p>
        <template v-if="step === 0">
          <p>
            產品待辦清單好了之後 ， 我們來召集 ScrumMaster 和開發團隊共同召開
            <span class="primary-color"> 短衝規劃會議（Sprint Planning）</span>
            。 短衝即是一個迭代 ， 具有固定時間限制 ， 我們會在這個會議中 ，
            決定要完成哪些工作事項來達到商業需求 ， 列出短衝待辦清單 （Sprint
            Backlog） ， 並由開發團隊在接下來的產品開發週期裡執行 。
          </p>
        </template>
        <div class="next_dialog_box">
          <img
            class="next_dialog_img"
            src="../../assets/images/gif/ic_continue_po.gif"
            alt="ic_continue_po"
          />
        </div>
      </div>
    </div>
    <div class="dialog__sm" v-if="step === 1 || step === 2">
      <div class="dialog__content__sm">
        <p class="tag__sm">MM</p>
        <template v-if="step === 1">
          <p>
            哦哦 ， 你是新來的前端吧 ！ 我是這次的
            <span class="primary-color"> ScrumMaster MM </span>
            ， 我的工作主要是促成開發團隊成員協作 、 引導團隊進行自省會議 ，
            提升團隊成員對 Scrum 瞭解 。
          </p>
        </template>
        <template v-if="step === 2">
          <p>
            這兩位是 EE 和 GG ， 是我們開發團隊的成員唷～ 我們團隊
            <span class="primary-color">一次 Sprint 週期是兩週</span>
            的時間 ， 依照我的觀察 ， 目前團隊可以負擔的點數 (Story Point)
            大約是<span class="primary-color">20 點</span>左右。
          </p>
        </template>
        <div class="next_dialog_box">
          <img
            class="next_dialog_img"
            src="../../assets/images/gif/ic_continue_sm.gif"
            alt="ic_continue_po"
          />
        </div>
      </div>
    </div>
    <div class="dialog__ee" v-if="step === 3 || step === 4">
      <div class="dialog__content__ee">
        <p class="tag__ee">EE</p>
        <template v-if="step === 3">
          <p>
            欸新來的 ， 你應該不知道點數是什麼意思吧ㄏㄏ ， 我來跟你介紹一下吧～
            <span class="primary-color"> Story Point</span> 目的是為了<span
              class="primary-color"
              >衡量速度</span
            >
            ， 是用大概花費的時間預估出的相對點數哦 。
          </p>
        </template>
        <template v-if="step === 4">
          <p>
            以
            <span class="primary-color">
              「 費氏數列 」 的 1 、2 、3 、5 、8 、13 、21</span
            >
            來估算各項 Story 的分數 。 Story Point 越小 ， 表示這個 Story
            花費時間越少 ； 越大 ， 花費時間則越多 。 如果出現了一個 21 分 ，
            可能表示這個 Story 太龐大 ， 需要再拆分細項執行唷 ！
          </p>
        </template>
        <div class="next_dialog_box">
          <img
            class="next_dialog_img"
            src="../../assets/images/gif/ic_continue_team1.gif"
            alt="ic_continue_po"
          />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  emits: ["next-step"],
  data() {
    return {
      step: 0,
    };
  },
  methods: {
    nextStep() {
      if (this.step === 0) {
        this.step = 1;
      } else if (this.step === 1) {
        this.step = 2;
      } else if (this.step === 2) {
        this.step = 3;
      } else if (this.step === 3) {
        this.step = 4;
        let timeImg = document.getElementsByClassName("time").item(0);
        timeImg.classList.add("hide");
      } else if (this.step === 4) {
        this.$emit("next-step", 3);
      }
    },
  },
  mounted() {
    setTimeout(() => {}, 500);
  },
};
</script>
<style lang="scss" scoped>
