    border-radius: 50%;
    background-color: #00ffe0;
    color: #ffffff;
    text-align: center;
    line-height: 80px;
    position: absolute;
    top: 50%;
    left: 60%;
    transform: translate(-50%, -50%);
    font-size: 44px;
    font-weight: 700;
    &13 {
