<template>
  <div class="container" @click="nextStep">
    <div class="role__box">
      <div class="role_team2_box">
        <img
          class="role_team2_hole"
          src="../../assets/images/role/hole.svg"
          alt="hole"
        />
        <img
          class="role_team2_img"
          src="../../assets/images/role/role_team2.png"
          alt="role"
        />
        <img
          class="role_team2_light"
          src="../../assets/images/role/role_team2_light.png"
          alt="role"
        />
      </div>
    </div>
    <div class="dialog__gg">
      <div class="dialog__content__gg">
        <p class="tag__gg">GG</p>
        <template v-if="step === 0">
          <p>
            嗚啊啊新來的 ， 你真的很幸運 ， 今天剛好是開發 B 組的 Retro ，
            你也來見識一下 ， 看看 Retro 都該做些什麼吧～～
          </p>
        </template>
        <template v-if="step === 1">
          <p>
            我們會在會議裡請團隊成員提出哪些是做得好的地方 、
            哪些可以繼續改善的地方 ？ 並記錄再 Confluence 中 。 Retro
            重點在於「正面表述」， 你也思考看看 ， 哪一些是適合 Retro 的回饋呢
            ？
          </p>
        </template>
        <template v-if="step === 2">
          <p>太酷ㄌ吧 ， 根本天才 ， 畢業之後不要忘記我唷＞＜ / ! ？</p>
        </template>
        <div class="next_dialog_box">
          <img
            class="next_dialog_img"
            src="../../assets/images/gif/ic_continue_team2.gif"
            alt="ic_continue_po"
          />
        </div>
      </div>
    </div>
    <div class="question_box" v-if="step === 1">
      <div class="question question_1">
        <p class="q_text">Q1.做得好的地方?</p>
        <div class="q_box">
          <div class="card">
            <img
              class="img1"
              src="../../assets/images/role/role_sm.png"
              alt=""
            />
            <input
              type="radio"
              v-model="answer[0].A"
              value="false"
              id="Q1_op1"
            />
            <label for="Q1_op1" class="label_style"
              ><span class="falseSpan"></span> 這次我幫了很多人救火耶～
            </label>
          </div>
          <div class="card">
            <img
              class="img2"
              src="../../assets/images/role/role_po.png"
              alt=""
            />
            <input
              type="radio"
              value="true"
              v-model="answer[0].A"
              id="Q1_op2"
            />
            <label for="Q1_op2" class="label_style"
              ><span></span> 大家在開發上都會互相 cover ，
              讓任務都有準在時間內完成 。</label
            >
          </div>
        </div>
      </div>
      <div class="question question_2">
        <p class="q_text">Q2.有哪些可以做得更好？</p>
        <div class="q_box">
          <div class="card">
            <img
              class="img3"
              src="../../assets/images/role/role_team1.png"
              alt=""
            />
            <input
              type="radio"
              value="true"
              v-model="answer[1].A"
              id="Q2_op1"
            />
            <label for="Q2_op1" class="label_style">
              <span class="radio_style"></span>
              可以記錄這次的開發時間 ， 讓預估團隊點數可以更精準 。
            </label>
          </div>
          <div class="card">
            <img
              class="img4"
              src="../../assets/images/role/role_team2.png"
              alt=""
            />
            <input
              type="radio"
              value="false"
              v-model="answer[1].A"
              id="Q2_op2"
            />
            <label for="Q2_op2" class="label_style">
              <span class="falseSpan"></span>
              開發時間預估不準確 ， 請後端下次改進 ， 避免 delay 到我 。
            </label>
          </div>
        </div>
      </div>
    </div>
  </div>
  <template v-if="step === 1">
    <button
      v-if="finished === true"
      type="button"
      class="primary-btn"
      v-on:click.stop="finishAction"
    ></button>
    <button
      v-else
      type="button"
      class="primary-btn invalid-btn"
      disabled
    ></button>
  </template>
  <div class="mask" v-on:click="nextPage">
    <button class="primary-btn-outline">點擊畫面任意處繼續</button>
  </div>
</template>
<script>
export default {
  emits: ["next-step"],
  data() {
    return {
      step: 0,
      answer: [{ A: "" }, { A: "" }],
      finished: false,
    };
  },
  methods: {
    nextStep() {
      if (this.step === 0) {
        this.step = 1;
      }
    },
    nextPage() {
      this.$emit("next-step", 6);
    },
    finishAction() {
      this.step = 2;
      let mask = document.getElementsByClassName("mask").item(0);
      let question = document.getElementsByClassName("question_box").item(0);
      setTimeout(() => {
        mask.classList.add("show");
        question.style.opacity = "0.2";
      }, 500);
    },
  },
  watch: {
    answer: {
      handler: function (val) {
        let arr = val.filter((item) => {
          return item.A === "true";
        });
        if (arr.length === 2) {
          this.finished = true;
        } else {
          this.finished = false;
        }
      },
      deep: true,
    },
  },
};
</script>
<style lang="scss" scoped>
.primary-btn {
  position: absolute;
  right: 40px;
  bottom: 10vh;
  z-index: 1000;
  &::after {
    content: "我選好了";
  }
  &::before {
    content: "我選好了";
  }
}

.invalid-btn {
  position: absolute;
  right: 40px;
  bottom: 10vh;
  z-index: 0;
  &::after {
    content: "我選好了";
  }
  &::before {
    content: "我選好了";
  }
}
.role__box {
  height: 300px;
}
.dialog__gg {
  position: absolute;
  width: 55vw;
  left: 5vw;
  top: 40px;
  z-index: 1000;
  &__content {
    padding: 40px 40px 40px 100px;
  }
  .tag__gg {
    position: absolute;
    color: #0a0d14;
    top: 40.5px;
    left: -7px;
    background: #ff5c00;
    padding: 4px 24px;
  }
  .next_dialog_box {
    margin-top: -43px;
    display: flex;
    justify-content: flex-end;
    img {
      width: 2rem;
      cursor: pointer;
    }
  }
}

.dialog__content__gg {
  padding: 40px 40px 40px 100px;
}

.question_box {
  position: relative;
  z-index: 1000;
  display: flex;
  padding: 0 120px;
  gap: 120px;
  .question {
    width: calc((100% - 120px) / 2);
    .q_text {
      background: #00ffe0;
      padding: 4px 32px;
      font-size: 32px;
      line-height: 48px;
      letter-spacing: 0.2rem;
      font-weight: 700;
      text-align: center;
    }
    .q_box {
      display: flex;
      justify-content: space-between;
      gap: 20px;
      .card {
        width: calc(100% / 2);
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }
    }
  }
  img {
    width: 165px;
    margin-top: 75px;
    &.img2,
    &.img3,
    &.img4 {
      transform: rotate(180deg);
    }
  }
}

input[type="radio"] {
  display: none;
}

input[type="radio"] + label span {
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  vertical-align: middle;
  background: rgba(10, 13, 20, 0.6);
  padding: 1px;
  position: relative;
  top: -70px;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
  &::after {
    content: "";
    width: 28px;
    height: 28px;
    border: 2px solid #00ffe0;
    position: absolute;
    border-radius: 50%;
    top: -4px;
    left: -4px;
  }
}

input[type="radio"]:checked + label span {
  background: #00ffe0;
}

input[type="radio"]:checked + label .falseSpan {
  background: #ff0000;
  &::after {
    border: 2px solid #ff0000;
  }
}

.label_style {
  position: relative;
  background: rgba(10, 13, 20, 0.6);
  border: 2px solid #00ffe0;
  border-radius: 20px;
  padding: 12px 20px;
  height: 132px;
  color: #ffffff;
  font-size: 20px;
  line-height: 36px;
  font-weight: 500;
  letter-spacing: 0.2rem;
  margin-top: 60px;
  cursor: pointer;
}

.mask {
  position: absolute;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 2050;
  background: linear-gradient(
    180deg,
    rgba(0, 255, 224, 0) 0%,
    rgba(0, 255, 224, 0.25) 40%,
    rgba(0, 255, 224, 0.45) 60%,
    rgba(0, 255, 224, 0.7) 80%,
    rgba(0, 255, 224, 0.9) 100%
  );
  display: none;
  &.show {
    display: block;
  }
  button {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
</style>
