          alt="role"
        />
        <img
          class="role_team1_light"
          src="../../assets/images/role/role_team1_light.png"
          alt="role"
        />
      </div>
      <div class="role_team2_box">
        <img
          class="role_team2_hole"
          src="../../assets/images/role/hole.svg"
          alt="hole"
        />
        <img
          class="role_team2_img"
          src="../../assets/images/role/role_team2.png"
          alt="role"
        />
        <img
          class="role_team2_light"
          src="../../assets/images/role/role_team2_light.png"
          alt="role"
        />
      </div>
    </div>
    <div class="dialog__gg">
      <div class="dialog__content__gg">
        <p class="tag__gg">GG</p>
        <template v-if="step === 0">
          <p>
            沒錯，如 EE 說的，我這邊已經把剛剛討論好的點數標上去囉～
            你來練習把任務排到短衝待辦清單吧 ！
          </p>
        </template>
        <template v-if="step === 1">
          <p>
            換你來試試看吧 ！ 把
            <span class="primary-color">「 產品待辦清單 」</span> 的項目拖進
            <span class="primary-color">「 開發Ａ組的短衝待辦清單 」</span>
            裡吧 ！ 提示 ： 置入兩項以上的 Story ， 點數總和不能超過團隊負擔上限
            20 點唷 ！
          </p>
        </template>
        <template v-if="step === 2">
          <p>噢嗚嗚 ， 太厲害ㄌ ！ 又完成了一關 ！ 還有下一關等著你 ！</p>
        </template>
        <div class="next_dialog_box">
          <img
            class="next_dialog_img"
            src="../../assets/images/gif/ic_continue_team2.gif"
            alt="ic_continue_po"
          />
        </div>
      </div>
    </div>
    <div class="dialog__ee" v-if="step === 0">
      <div class="dialog__content__ee">
        <p class="tag__ee">EE</p>
        <template v-if="step === 0">
          <p>
            By the way , 我們平常管理任務是使用
            <img src="../../assets/images/AttachedPic/jira_w.svg" alt="" />
            這套軟體 ， 你有時間記得先去註冊和熟悉唷 !
          </p>
        </template>
        <div class="next_dialog_box">
          <img
            class="next_dialog_img"
            src="../../assets/images/gif/ic_continue_team1.gif"
            alt="ic_continue_po"
          />
        </div>
      </div>
    </div>
    <template v-if="step === 1 || step === 2">
      <div class="card">
        <div class="card__header">
          <p class="title">產品待辦清單</p>
          <p class="sub_title">Product Backlog</p>
        </div>
        <div class="card__body">
          <draggable
            :list="dataList"
            :group="group_drag"
            class="list"
            item-key="id"
            @end="onEnd"
            :emptyInsertThreshold="436"
          >
            <template #item="{ element }">
              <div class="list__item drag">
                <p class="num">{{ element.num }}</p>
                <p class="text">{{ element.text }}</p>
              </div>
            </template>
          </draggable>
        </div>
      </div>
      <div class="card target">
        <div class="card__header">
          <p class="title">開發Ａ組的短衝待辦清單</p>
          <p class="sub_title">Sprint Backlog</p>
        </div>
        <div class="card__body">
          <draggable
            :list="targetList"
            :group="group_target"
            class="list draggable__target"
            item-key="id"
            @end="onTargetEnd"
            :emptyInsertThreshold="436"
          >
            <template #item="{ element }">
              <div class="list__item" :class="element.style">
                <p
                  v-if="element.text !== '' || element.text !== undefined"
                  class="num"
                >
                  {{ element.num }}
                </p>
                <p class="text">{{ element.text }}</p>
              </div>
            </template>
          </draggable>
        </div>
        <div class="card__footer">
          <div :style="styleObject"></div>
          <span>{{ point }} / 20(5人)</span>
        </div>
      </div>
    </template>
    <button
      v-if="step === 0"
      type="button"
      class="primary-btn"
      v-on:click.stop="practiceAction"
    ></button>
    <button
      v-if="step === 1"
      type="button"
      :class="[
        isSprint === true
          ? 'primary-btn sprint-btn'
          : 'primary-btn invalid-btn',
      ]"
      v-on:click.stop="startSprint"
    ></button>
  </div>
  <div class="mask" v-on:click="nextPage">
    <button class="primary-btn-outline">點擊畫面任意處繼續</button>
  </div>
</template>
<script>
import draggable from "vuedraggable";
export default {
  components: {
    draggable,
  },
  emits: ["next-step"],
  data() {
    return {
      point: 0,
      step: 0,
      dataList: [
        {
          text: "後台職缺管理功能（資訊上架、下架、顯示應徵者資料）",
          id: 0,
          num: 8,
          style: "target_style",
        },
        {
          text: "會員系統（登入、註冊、權限管理）",
          id: 1,
          num: 5,
          style: "target_style",
        },
        {
          text: "應徵者的線上履歷編輯器",
          id: 2,
          style: "target_style",
          num: 13,
        },
        { text: "前台職缺列表、應徵", id: 3, style: "target_style", num: 8 },
      ],
      targetList: [],
      group_target: {
        name: "targetBox",
        put: true,
        pull: () => {},
      },
      group_drag: {
        name: "dragItem",
        put: true,
        pull: () => {},
      },
      styleObject: {
        background: "#FFC700",
        width: "16px",
        height: "16px",
        borderRadius: "50px",
        margin: "2px",
        display: "inline-block",
        float: "left",
      },
      isSprint: false,
    };
  },
  methods: {
    practiceAction() {
      this.step = 1;
    },
    startSprint() {
      this.step = 2;
      let mask = document.getElementsByClassName("mask").item(0);
      setTimeout(() => {
        mask.classList.add("show");
      }, 500);
      let list1 = document.getElementsByClassName("card").item(0);
      let list2 = document.getElementsByClassName("card").item(1);
      setTimeout(() => {
        list1.style.opacity = "0.3";
        list2.style.opacity = "0.3";
      }, 500);
    },
    nextPage() {
      this.$emit("next-step", 4);
    },
    onEnd(e) {
      let newArr = e.to.__draggable_component__.list;
      let index = newArr.findIndex((element) => element.text === "");
      if (index !== -1) newArr.splice(index, 1);
      this.answer = newArr.every((item, i) => item.id === i);

      let width = (this.point / 20) * 100;
      if (width === 0) {
        this.styleObject.width = "16px";
        this.styleObject.background = "#FFC700";
      } else if (width > 100) {
        this.styleObject.width = "99%";
        this.styleObject.background = "#FF0000";
      } else {
        this.styleObject.width = width + "%";
        this.styleObject.background = "#FFC700";
      }
    },
    onTargetEnd() {
      let width = (this.point / 20) * 100;
      if (width === 0) {
        this.styleObject.width = "16px";
        this.styleObject.background = "#FFC700";
      } else if (width > 100) {
        this.styleObject.width = "99%";
        this.styleObject.background = "#FF0000";
      } else {
        this.styleObject.width = width + "%";
        this.styleObject.background = "#FFC700";
      }
    },
  },
  watch: {
    targetList: {
      handler: function (val) {
        let total = 0;
        val.forEach((item) => {
          total += item.num;
        });
        this.point = total;
        if (val.length >= 2 && total <= 20) {
          this.isSprint = true;
        } else if (val.length < 2 || total > 20) {
          this.isSprint = false;
        }
      },
      deep: true,
    },
  },
};
</script>
<style lang="scss" scoped>
