<template>
  <div class="bg_village"></div>
  <Loading v-if="isLoading" :color="color" :value="value" />
  <EntranceView v-if="step === 0" @next-step="nextStep"></EntranceView>
  <Step1 v-if="step === 1" @next-step="nextStep"></Step1>
  <Step2 v-if="step === 2" @next-step="nextStep"></Step2>
  <Step3 v-if="step === 3" @next-step="nextStep"></Step3>
  <Step4 v-if="step === 4" @next-step="nextStep"></Step4>
  <Step5 v-if="step === 5" @next-step="nextStep"></Step5>
  <Step6 v-if="step === 6"></Step6>
  <Footer></Footer>
</template>
<script>
import Loading from "../components/LoadingView.vue";
import Footer from "../components/FooterView.vue";
import EntranceView from "../components/pages/EntranceView.vue";
import Step1 from "../components/pages/Step1View.vue";
import Step2 from "../components/pages/Step2View.vue";
import Step3 from "../components/pages/Step3View.vue";
import Step4 from "../components/pages/Step4View.vue";
import Step5 from "../components/pages/Step5View.vue";
import Step6 from "../components/pages/Step6View.vue";

export default {
  name: "App",
  components: {
    Loading,
    Footer,
    EntranceView,
    Step1,
    Step2,
    Step3,
    Step4,
    Step5,
    Step6,
  },
  data() {
    return {
      isLoading: true,
      step: -1,
      color: "linear-gradient(270deg, #00ffe0 0%, rgba(0, 255, 224, 0) 100%)",
      value: 100,
    };
  },
  methods: {
    loadingHandler() {
      setTimeout(() => {
        this.isLoading = false;
        this.nextStep(0);
      }, 3550);
    },
    nextStep(step) {
      this.step = step;
    },
  },
  mounted() {
    this.loadingHandler();
  },
};
</script>
<style lang="scss">
.bg_village {
  background-image: url("../assets/images/bg/bg_village.png");
  background-size: cover;
  width: 100vw;
  height: 100vh;
  background-position: center center;
  position: absolute;
}
</style>
