<template>
  <div class="container" @click="nextAction">
    <div class="role_po__box">
      <img
        class="role_po_hole"
        src="../../assets/images/role/hole.svg"
        alt="hole"
      />
      <img
        class="role_po_img"
        src="../../assets/images/role/role_po.png"
        alt="role_po"
      />
      <img
        class="role_po_light"
        src="../../assets/images/role/role_po_light.png"
        alt="role_po"
      />
    </div>
    <div class="dialog">
      <div class="dialog__content">
        <p class="tag">PO</p>
        <template v-if="step === 0">
          <p>
            <span class="primary-color">\ 碰 /</span> 我是短衝小精靈 ， 開發 A
            組的 PO 。
          </p>
          <p>
            <span class="primary-color"
              >PO 也就是產品負責人（Product Owner）</span
            >
            ， 產品負責人會負責評估產品待辦清單的價值與重要性，
            依序排列要執行的優先順序 ， 對齊產品目標 。
            最後排出產品待辦清單（Product Backlog） 唷 ！
          </p>
        </template>
        <template v-if="step === 1">
          <p>
            剛好我最近手邊有一個 「 人才招募系統 」 的案子 ， 我才剛列出了
            <span class="primary-color"> 「 產品需求清單 」</span> 。
            既然你都來了 ， 來試試看調整產品優先度 ， 排出產品待辦清單吧 ！
          </p>
        </template>
        <template v-if="step === 2">
          <p>
            在這階段我們要把需求放進產品待辦清單 ， 並調整其優先順序 。 對了 ！
            我們公司也推薦使用
            <img
              src="../../assets/images/AttachedPic/jira_w.svg"
              alt="jira_w"
            />
            來做任務的管理呢 ！
          </p>
        </template>
        <template v-if="step === 3">
          <p>
            <span class="h2">換你來試試看吧 ！</span><br />
            提示 ： 請把需求拖移至產品待辦清單 ， 並調整其優先順序 。
          </p>
        </template>
        <template v-if="step === 4">
          <p>哇喔完成惹 ， 尼太棒ㄌ！ 我們繼續吧 ！</p>
        </template>
        <div class="next_step_box" v-if="step === 1">
          <img
            class=""
            src="../../assets/images/icon/ic_continue_po.svg"
            alt="next_step_icon"
          />
        </div>
        <div class="next_step_box" v-if="step === 2" v-on:click="tryAction">
          <img
            class=""
            src="../../assets/images/icon/ic_continue_po.svg"
            alt="next_step_icon"
          />
        </div>
      </div>
    </div>
    <div v-if="step === 0" class="btn__box">
      <button class="primary-btn-outline">點擊畫面任意處繼續</button>
    </div>
    <div v-if="step === 1">
      <button
        type="button"
        class="primary-btn"
        v-on:click.stop="readyAction"
      ></button>
    </div>
    <div v-if="step === 3">
      <button v-if="!answer" type="button" class="invalid-btn"></button>
      <button
        v-else
        type="button"
        class="primary-btn nextStep"
        v-on:click.stop="finishAction"
      ></button>
    </div>
    <template v-if="step === 2">
      <div class="card">
        <div class="card__header">
          <p class="title">產品待辦清單</p>
          <p class="sub_title">Product Backlog</p>
        </div>
        <div class="card__body">
          <div class="arrow__box">
            <p>高</p>
            <img
              class="arrow"
              src="../../assets/images/AttachedPic/arrow.svg"
              alt="arrow"
            />
            <p>低</p>
          </div>
          <div class="list">
            <div
              v-for="item in demoList"
              v-bind:key="item.id"
              class="list__item"
            >
              <p>{{ item.text }}</p>
              <img
                class="demo_hand"
                src="../../assets/images/AttachedPic/hand.svg"
                alt="hand"
              />
            </div>
          </div>
        </div>
        <img
          class="demo_arrow"
          id="demo_arrow"
          src="../../assets/images/AttachedPic/arrow.svg"
          alt="arrow"
        />
        <div class="draggable">
          <div
            v-for="item in dataList"
            v-bind:key="item.id"
            class="draggable__item opacity"
          >
            {{ item.text }}
          </div>
        </div>
      </div>
    </template>
    <template v-if="step === 3">
      <div class="card">
        <div class="card__header">
          <p class="title">產品待辦清單</p>
          <p class="sub_title">Product Backlog</p>
        </div>
        <div class="card__body">
          <div class="arrow__box">
            <p>高</p>
            <img
              class="arrow"
              src="../../assets/images/AttachedPic/arrow.svg"
              alt="arrow"
            />
            <p>低</p>
          </div>
          <draggable
            :list="targetList"
            :group="group_target"
            class="list draggable__target"
            item-key="id"
            @end="onEnd"
          >
            <template #item="{ element }">
              <div class="list__item" :class="element.style">
                <p class="">{{ element.text }}</p>
              </div>
            </template>
          </draggable>
        </div>
        <div class="draggable">
          <draggable
            :list="dataList"
            :group="group_drag"
            item-key="id"
            @end="onEnd"
          >
            <template #item="{ element }">
              <div class="draggable__item">
                {{ element.text }}
              </div>
            </template>
          </draggable>
        </div>
      </div>
    </template>
  </div>
  <div class="mask" v-on:click="nextPage">
    <button class="primary-btn-outline">點擊畫面任意處繼續</button>
  </div>
</template>
<script>
import draggable from "vuedraggable";
export default {
  emits: ["next-step"],
  data() {
    return {
      step: 0,
      demoList: [{ text: "前台職缺列表、應徵" }, "", "", ""],
      targetList: [
        { text: "", tarID: 0, style: "" },
        { text: "", tarID: 1, style: "" },
        { text: "", tarID: 2, style: "" },
        { text: "", tarID: 3, style: "" },
      ],
      dataList: [
        {
          text: "後台職缺管理功能（資訊上架、下架、顯示應徵者資料）",
          id: 0,
          style: "target_style",
        },
        {
          text: "會員系統（登入、註冊、權限管理）",
          id: 1,
          style: "target_style",
        },
        { text: "應徵者的線上履歷編輯器", id: 2, style: "target_style" },
        { text: "前台職缺列表、應徵", id: 3, style: "target_style" },
      ],
      group_target: {
        name: "targetBox",
        put: true, //可以拖入
        pull: () => {},
      },
      group_drag: {
        name: "dragItem",
        put: true,
      },
      isDemo: false,
      answer: false,
    };
  },
  components: {
    draggable,
  },
  methods: {
    nextAction() {
      if (!this.isDemo) {
        this.step = 1;
        this.isDemo = true;
      }
    },
    readyAction() {
      this.step = 2;
      this.$nextTick(() => {
        let arrow = document.getElementById("demo_arrow");
        let listItem = document.getElementsByClassName("list__item").item(0);
        let hand = document.getElementsByClassName("demo_hand").item(0);
        setTimeout(() => {
          arrow.classList.add("visible");
          hand.classList.add("visible");
        }, 500);
        setTimeout(() => {
          listItem.classList.add("demo_item");
        }, 900);
      });
    },
    tryAction() {
      this.step = 3;
    },
    onEnd(e) {
      let newArr = e.to.__draggable_component__.list;
      let index = newArr.findIndex((element) => element.text === "");
      if (index !== -1) newArr.splice(index, 1);
      this.answer = newArr.every((item, i) => item.id === i);
    },
    finishAction() {
      this.step = 4;
      let mask = document.getElementsByClassName("mask").item(0);
      setTimeout(() => {
        mask.classList.add("show");
      }, 500);
    },
    nextPage() {
      this.step = 5;
      this.$emit("next-step", 2);
    },
  },
};
</script>
<style lang="scss" scoped>
.mask {
  position: absolute;
  width: 100vw;
  height: 100vh;
  z-index: 950;
  background: linear-gradient(
    180deg,
    rgba(0, 255, 224, 0) 0%,
    rgba(0, 255, 224, 0.25) 60%,
    rgba(0, 255, 224, 0.45) 80%,
    rgba(0, 255, 224, 0.7) 90%,
    rgba(0, 255, 224, 0.9) 100%
  );
  display: none;
  &.show {
    display: block;
  }
  button {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.dialog {
  position: absolute;
  width: 65vw;
  left: 30vw;
  top: 40px;
  z-index: 1000;
  &__content {
    padding: 40px 40px 40px 100px;
  }
  .tag {
    position: absolute;
    color: #0a0d14;
    top: 40.5px;
    left: -7px;
    background: #00ffe0;
    padding: 4px 24px;
  }
}

.next_step_box {
  margin-top: -43px;
  display: flex;
  justify-content: flex-end;
  animation: jumpBoxHandler 1s infinite;
  img {
    width: 2rem;
    cursor: pointer;
  }
}

@keyframes jumpBoxHandler {
  0% {
    transform: translate(0px, 0px);
  }
  50% {
    transform: translate(0px, 5px);
  }
  100% {
    transform: translate(0px, 0px);
  }
}

.btn__box {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  position: relative;
  z-index: 5;
}

.primary-btn {
  position: absolute;
  right: 40px;
  bottom: 10vh;
  z-index: 6;
  &::after {
    content: "準備好了";
  }
  &::before {
    content: "準備好了";
  }
  &.nextStep {
    &::after {
      content: "我完成了";
    }
    &::before {
      content: "我完成了";
    }
  }
}

.invalid-btn {
  position: absolute;
  right: 40px;
  bottom: 10vh;
  z-index: 6;
  &::after {
    content: "我完成了";
  }
  &::before {
    content: "我完成了";
  }
}

.card {
  height: 596px;
  width: 500px;
  background-color: rgba(0, 255, 224, 0.3);
  z-index: 6;
  position: absolute;
  top: 30vh;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 20px;
  &__header {
    background-color: #00ffe0;
    text-align: center;
    padding: 15px;
    border-radius: 20px 20px 0 0;
    .title {
      color: rgba(10, 13, 20, 1);
      font-size: 32px;
      font-weight: 700;
      letter-spacing: 0.2rem;
    }
    .sub_title {
      color: rgba(0, 141, 150, 1);
      font-size: 16px;
      font-weight: 700;
    }
  }
  &__body {
    border-radius: 0 0 20px 20px;
    display: flex;
    padding: 32px;
    color: #ffffff;
    font-size: 16px;
    font-weight: 700;
    .arrow__box {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 60px;
      margin-left: -110px;
    }
    .arrow {
      height: 280px;
      transform: rotate(219deg);
    }
    .list {
      display: flex;
      flex-direction: column;
      gap: 16px;
      margin-left: -60px;
      &__item {
        border: 2px dashed #00ffe0;
        border-radius: 20px;
        width: 380px;
        padding: 12px;
        font-size: 20px;
        line-height: 36px;
        letter-spacing: 0.2rem;
        font-weight: 500;
        height: 96px;
      }
      .hide {
        display: none;
      }
    }
  }
}

.draggable {
  &__item {
    position: absolute;
    border: 2px solid #00ffe0;
    border-radius: 20px;
    padding: 12px 24px;
    font-size: 20px;
    line-height: 36px;
    letter-spacing: 0.2rem;
    font-weight: 500;
    color: #ffffff;
    cursor: pointer;
    &:not(:nth-child(4)).opacity {
      opacity: 0.5;
    }
    &:nth-child(1) {
      top: 40vh;
      left: -25vw;
      width: 324px;
    }
    &:nth-child(2) {
      top: 18vh;
      right: -23vw;
    }
    &:nth-child(3) {
      top: 15vh;
      left: -18vw;
    }
    &:nth-child(4) {
      top: 40vh;
      right: -17vw;
    }
  }
}

.demo_arrow {
  position: absolute;
  top: 20vh;
  right: -5vw;
  opacity: 0;
  transition: opacity 0.5s;
  &.visible {
    opacity: 1;
  }
}

.card__body .list__item {
  position: relative;
  &.demo_item,
  &.target_style {
    border: 2px solid #00ffe0;
    background: rgba(10, 13, 20, 0.6);
  }
  &:not(:first-child) {
    .demo_hand {
      display: none;
    }
  }
  .demo_hand {
    position: absolute;
    right: 0;
    top: 120px;
    opacity: 0;
    transition: all 0.5s;
    &.visible {
      top: 70px;
      right: 60px;
      opacity: 1;
    }
  }
}
</style>
